<template>
	<div id="store-manage">
		<public-header title="店铺管理"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell is-link :to="util.getUrl({path: '/pages/shop/basic', query: {id: id}})">
					<i class="icon icon-form c-primary" slot="icon"></i>
					<div slot="title" >基本信息</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/shop/qualification', query: {id: id}})">
					<i class="icon icon-shop c-info" slot="icon"></i>
					<div slot="title">营业资质</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'notice', id: id}})">
					<i class="icon icon-newshot c-primary" slot="icon"></i>
					<div slot="title">商户公告</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'tips', id: id}})">
					<i class="icon icon-information c-info" slot="icon"></i>
					<div slot="title">商品列表页提示</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell is-link :to="util.getUrl({path: '/pages/shop/account', query: {id: id}})">
					<i class="icon icon-waimai font-20 c-primary" slot="icon"></i>
					<div slot="title" >账户设置</div>
				</van-cell>
				<van-cell is-link :to="util.getUrl({path: '/pages/shop/pill', query: {id: id}})">
					<i class="icon icon-pay c-info" slot="icon"></i>
					<div slot="title" >支付设置</div>
				</van-cell>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				id: 0
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				getData({
					vue: this,
					url: 'manage/shop/index/index',
					success: function(res) {
						this.store = res.store;
						this.manager = res.manager;
						this.stat = res.stat;
						this.notice = res.notice;
						//this.order_notice_num = res.order_notice_num;
					}
				});
			}

		},
		created() {
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-manage .van-cell .icon{
		margin-right: 7px;
		font-size: 18px;
	}
</style>


